import React, { useState } from "react";
import Style from "./Navbar.module.scss";
import Toggler from "./home/Toggler";
import SwitchCli from "./home/SwitchCli";
import { Link, useLocation } from "react-router-dom";
import { Box } from "@mui/material";
import { info } from "../../../info/Info";

import logoImage from "../../../img/ja-logo.png";

const links = [
  {
    name: "Home",
    to: "/gui/home",
    active: "home",
  },
  {
    name: info.initials,
    type: "initials",
    to: "/gui/home",
    active: "home",
  },
];

export default function Navbar({ darkMode, handleClick }) {
  const location = useLocation();
  const [active, setActive] = useState(
    location.pathname === "/" ? "home" : location.pathname.slice(1, location.pathname.length)
  );

  return (
    <Box component={"nav"} width={"100%"}>
      <Box
        component={"ul"}
        display={"flex"}
        justifyContent={"center"}
        alignItems={"center"}
        gap={{ xs: "2rem", md: "8rem" }}
        fontSize={"1rem"}
      >
        {links.map((link, index) => (
          <Box
            key={index}
            component={"li"}
            className={(link.active === active && !link.type) && Style.active}
            sx={{ borderImageSource: info.gradient }}
          >
            <Link
              to={link.to}
              onClick={() => setActive(link.active)}
              className={Style.link}
            >
              {!link.type ? (
                <p style={{ padding: "0.3rem 0" }}>{link.name}</p>
              ) : (
                <img src={logoImage} alt="Logo" style={{ width: "3rem", height: "auto", padding: "0.4rem 0" }} />
              )}
            </Link>
          </Box>
        ))}
        <li>
          <Toggler darkMode={darkMode} handleClick={handleClick} />
        </li>
        <li>
          <SwitchCli />
        </li>
      </Box>
    </Box>
  );
}
